<template>
  <div class="login">
    <transition name="form-fade" mode="in-out">
      <section class="form_contianer" v-show="showLogin">
        <!-- <div class="login_logo"><img src="../../assets/image/logo.png" alt=""></div> -->
        <h2>{{$t('login.title')}}</h2>
        <a-form
          :layout="formLayout"
          @submit="submitLogin"
          :autoFormCreate="(form)=>{this.form = form}"
          :hideRequiredMark="true"
        >
          <a-form-item
            :label="this.$t('login.user')"
            fieldDecoratorId="account"
            :fieldDecoratorOptions="{rules: rules.account}"
          >
            <a-input :placeholder="$t('login.user')" @focus="blueUsername()">
              <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
            </a-input>
          </a-form-item>

          <p class="loginErrorInfor" v-if="hasUserError">{{$t('error.userNotexist')}}</p>
          <a-form-item
            :label="this.$t('login.password')"
            fieldDecoratorId="password"
            :fieldDecoratorOptions="{rules: rules.password}"
          >
            <a-input type="password" :placeholder="$t('login.password')" @focus="bluePassword()">
              <!-- <a-icon slot="prefix" type='lock' style="color:rgba(0,0,0,.25)"/> -->
            </a-input>
          </a-form-item>
          <p class="loginErrorInfor" v-if="haspasswordError">{{$t('error.errorPassword')}}</p>
          <a-form-item class="submitBtn">
            <a-button type="primary" htmlType="submit">{{$t('login.title')}}</a-button>
          </a-form-item>
        </a-form>
        <p class="forget_lang">
          <lang class="switchLang" :showIcon="true"></lang>
        </p>
        <!-- <a-button @click="gotoRegister"> 前往注册</a-button> -->
      </section>
    </transition>
  </div>
</template>

<script>
import lang from "@/components/change-lang";
// import { mapGetters } from "vuex";
import Cookies from "@/js/cookies";

export default {
  data() {
    return {
      formLayout: "vertical",
      loginForm: {
        password: "admin",
        account: "123"
      },
      rules: {
        password: [
          {
            required: true,
            message: this.$t("login.errorPassword"),
            trigger: "blur"
          }
        ],
        account: [
          {
            required: true,
            message: this.$t("login.errorPassword"),
            trigger: "blur"
          }
        ]
      },
      hasUserError: false,
      haspasswordError: false,
      showLogin: false
    };
  },
  components: {
    lang
  },

  methods: {
    submitLogin(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values);
          this.$store.dispatch("loginIn", values).then(
            res => {
              console.log(res);
              Cookies.set("menu", "companyInfo");
              Cookies.set("openMenu","identityCheck");
              let avatar="http://kotadata.oss-ap-southeast-5.aliyuncs.com"+res.data.icon;
              Cookies.set("avatar", avatar);
              this.$router.push({ name: "companyInfo" });
            },
            error => {
              console.log(error);
              if(error.data.code==400){    //密码错误
                  // if(error.data.data)
                    this.haspasswordError=true;
              }
            }
          );
        }
      });
    },
    blueUsername() {
      this.hasUserError = false;
    },
    bluePassword() {
      this.haspasswordError = false;
    }
    // gotoRegister(){
    //   console.log(1111);
    //   this.$router.push('/register');
    // }
  },
  mounted() {
    this.showLogin = true;
  }
};
</script>

<style lang="less">
@import "../../css/login.less";
</style>





